import React from "react";
import Footer from "../Footer";
import NavBar from "../Navbar";
import {useScrollToTop} from '../ScrollToTop'

function Privacy() {
  const setScrollToTop = useScrollToTop(true);
  return (
    <div onClick={() => setScrollToTop(true)}>
      <NavBar />
      <div className="container shadow-lg pt-5 pb-5 ps-3 pe-3 mb-5 bg-body rounded">
        <h3>Privacy Policy </h3>
        <p className="fw-bold mb-2">Last updated July 06, 2022</p>
        <p>
          This privacy notice for E-learning (<b>'Company'</b>, <b>'we'</b>,{" "}
          <b>'us'</b>, or <b>'our'</b>,), describes how and why we might
          collect, store, use, and/or share (<b>'process'</b>) your information
          when you use our services (<b>'Services'</b>), such as when you:
          <ul>
            <li>
              Visit our website at{" "}
              <a href="www.elearning.com" className="text-decoration-none">
                www.elearning.com
              </a>
              , or any website of ours that links to this privacy notice
            </li>
            <li>
              Engage with us in other related ways, including any sales,
              marketing, or events
            </li>
          </ul>
        </p>

        <p className="mb-3">
          <b>Questions or concerns?</b> Reading this privacy notice will help
          you understand your privacy rights and choices. If you do not agree
          with our policies and practices, please do not use our Services. If
          you still have any questions or concerns, please contact us at
          oriontyweb@gmail.com.
        </p>
        <h6 className="fw-bold">SUMMARY OF KEY POINTS</h6>
        <p className="mb-3">
          This summary provides key points from our privacy notice, but you can
          find out more details about any of these topics by clicking the link
          following each key point or by using our table of contents below to
          find the section you are looking for.
        </p>

        <p className="mb-3">
          <b>What personal information do we process?</b> When you visit, use,
          or navigate our Services, we may process personal information
          depending on how you interact with E-learning and the Services, the
          choices you make, and the products and features you use.
        </p>

        <p className="mb-3">
          <b>Do we process any sensitive personal information?</b> We do not
          process sensitive personal information.
        </p>
        <p className="mb-3">
          <b> Do we receive any information from third parties?</b> We may
          receive information from public databases, marketing partners, social
          media platforms, and other outside sources.
        </p>

        <p>
          <b> How do we process your information?</b> We process your
          information to provide, improve, and administer our Services,
          communicate with you, for security and fraud prevention, and to comply
          with law. We may also process your information for other purposes with
          your consent. We process your information only when we have a valid
          legal reason to do so
        </p>

        <p>
          <b>
            {" "}
            In what situations and with which parties do we share personal
            information?
          </b>{" "}
          We may share information in specific situations and with specific
          third parties.
        </p>
        <p>
          <b> How do we keep your information safe?</b> We have organisational
          and technical processes and procedures in place to protect your
          personal information. However, no electronic transmission over the
          internet or information storage technology can be guaranteed to be
          100% secure, so we cannot promise or guarantee that hackers,
          cybercriminals, or other unauthorised third parties will not be able
          to defeat our security and improperly collect, access, steal, or
          modify your information
        </p>
        <p>
          <b>What are your rights? </b> Depending on where you are located
          geographically, the applicable privacy law may mean you have certain
          rights regarding your personal information.
        </p>

        <h2>TABLE OF CONTENTS</h2>
        <div className="mb-5">
          <ol>
            <li>WHAT INFORMATION DO WE COLLECT ?</li>
            <li>HOW DO WE PROGRESS YOUR INFORMATION ?</li>
            <li>
              WHAT LEGAL BASES DO WE RELY ON TO PROCESS YOUR PERSONAL
              INFORMATION
            </li>
            <li>DO WE USE COOKIES AND OTHER TRACKING TECHNOLOGIES ?</li>
            <li>HOW DO WE HANDLE YOUR SOCIAL</li>
            <li>HOW LONG DO WE KEEP YOUR INFORMATION ?</li>
            <li>HOW DO WE KEEP YOUR INFORMATION SAFE ?</li>
            <li>WHAT ARE YOUR PRIVACY RIGHTS ?</li>
            <li>CONTROLS FOR DO NOT TRACK FEATURES</li>
            <li>DO CALIFORNIA RESIDENTS HAVE SPECIFIC PRIVACY RIGHTS ?</li>
            <li>DO WE MAKE UPDATES TO THIS NOTICE ?</li>
            <li>HOW CAN YOU CONTACT US ABOUT THIS NOTICE ?</li>
            <li>
              HOW CAN YOU REVIEW UPDATE OR DELETE THE DATA WE COLLECT FROM YOU ?
            </li>
          </ol>
        </div>

        <div>
          <ol>
            <li className="fw-bold">WHAT INFORMATION DO WE COLLECT ?</li>

            <h6>
              <b>Personal information you disclose to us</b>
            </h6>
            <p>
              <i>
                <b>In Short:</b> We collect personal information that you
                provide to us.
              </i>
            </p>
            <p>
              We collect personal information that you voluntarily provide to us
              when you register on the Services, express an interest in
              obtaining information about us or our products and Services, when
              you participate in activities on the Services, or otherwise when
              you contact us.
            </p>

            <p>
              <b>Personal Information Provided by You.</b> The personal
              information that we collect depends on the context of your
              interactions with us and the Services, the choices you make, and
              the products and features you use. The personal information we
              collect may include the following:
            </p>
            <ol>
              <li>names</li>
              <li>phone numbers</li>
              <li>email addresses</li>
              <li>passwords</li>
              <li>usernames</li>
              <li>debit/credit card numbers</li>
            </ol>
            <p>
              <b>Sensitive Information. </b> We do not process sensitive
              information.
            </p>
            <p>
              <b>Social Media Login Data.</b> We may provide you with the option
              to register with us using your existing social media account
              details, like your Facebook, Twitter, or other social media
              account. If you choose to register in this way, we will collect
              the information described in the section called 'HOW DO WE HANDLE
              YOUR SOCIAL LOGINS?' below.
            </p>
            <p>
              All personal information that you provide to us must be true,
              complete, and accurate, and you must notify us of any changes to
              such personal information.
            </p>
            <p>
              <b>Information collected from other sources</b>
              <p>
                <b>In Short:</b> We may collect limited data from public
                databases, marketing partners, social media platforms, and other
                outside sources.
              </p>
              <p>
                In order to enhance our ability to provide relevant marketing,
                offers, and services to you and update our records, we may
                obtain information about you from other sources, such as public
                databases, joint marketing partners, affiliate programs, data
                providers, social media platforms, and from other third parties.
                This information includes mailing addresses, job titles, email
                addresses, phone numbers, intent data (or user behaviour data),
                Internet Protocol (IP) addresses, social media profiles, social
                media URLs, and custom profiles, for purposes of targeted
                advertising and event promotion. If you interact with us on a
                social media platform using your social media account (e.g.
                Facebook or Twitter), we receive personal information about you
                such as your name, email address, and gender. Any personal
                information that we collect from your social media account
                depends on your social media account's privacy settings.
              </p>
            </p>

            <li className="fw-bold"> HOW DO WE PROCESS YOUR INFORMATION?</li>

            <p>
              <p>
                <b>In Short:</b> We process your information to provide,
                improve, and administer our Services, communicate with you, for
                security and fraud prevention, and to comply with law. We may
                also process your information for other purposes with your
                consent.
              </p>
              <p>
                We process your personal information for a variety of reasons,
                depending on how you interact with our Services, including:
              </p>
              <ul>
                <li>
                  <b>
                    To facilitate account creation and authentication and
                    otherwise manage user accounts.
                  </b>
                  We may process your information so you can create and log in
                  to your account, as well as keep your account in working
                  order.
                </li>
                <li>
                  <b>To save or protect an individual's vital interest.</b>
                  We may process your information when necessary to save or
                  protect an individual’s vital interest, such as to prevent
                  harm.
                </li>
              </ul>
            </p>

            <li className="fw-bold">
              {" "}
              WHAT LEGAL BASES DO WE RELY ON TO PROCESS YOUR INFORMATION?
            </li>
            <p>
              <p>
                <b>
                  <em>In short: </em>
                </b>
                <em>
                  We only process your personal information when we believe it
                  is necessary and we have a valid legal reason (i.e. legal
                  basis) to do so under applicable law, like with your consent,
                  to comply with laws, to provide you with services to enter
                  into or fulfil our contractual obligations, to protect your
                  rights, or to fulfil our legitimate business interests.
                </em>
                <p>
                  <b>
                    <u>
                      <i>
                        If you are located in the EU or UK, this section applies
                        to you.
                      </i>
                    </u>
                  </b>
                </p>
              </p>

              <p>
                The General Data Protection Regulation (GDPR) and UK GDPR
                require us to explain the valid legal bases we rely on in order
                to process your personal information. As such, we may rely on
                the following legal bases to process your personal information:
              </p>

              <ul>
                <li>
                  <b>Consent. </b>
                  We may process your information if you have given us
                  permission (i.e. consent) to use your personal information for
                  a specific purpose. You can withdraw your consent at any time.
                </li>
                <li>
                  <b>Legal Obligations. </b>
                  We may process your information where we believe it is
                  necessary for compliance with our legal obligations, such as
                  to cooperate with a law enforcement body or regulatory agency,
                  exercise or defend our legal rights, or disclose your
                  information as evidence in litigation in which we are
                  involved.
                </li>
                <li>
                  <b>Vital Interests. </b>
                  We may process your information where we believe it is
                  necessary to protect your vital interests or the vital
                  interests of a third party, such as situations involving
                  potential threats to the safety of any person.
                </li>
              </ul>
              <p>
                <b>
                  <u>
                    <i>
                      If you are located in Canada, this section applies to you.
                    </i>
                  </u>
                </b>
              </p>

              <p>
                We may process your information if you have given us specific
                permission (i.e. express consent) to use your personal
                information for a specific purpose, or in situations where your
                permission can be inferred (i.e. implied consent). You can
                withdraw your consent at any time.
              </p>
              <p>
                In some exceptional cases, we may be legally permitted under
                applicable law to process your information without your consent,
                including, for example:
              </p>
              <ul>
                <li>
                  If collection is clearly in the interests of an individual and
                  consent cannot be obtained in a timely way
                </li>
                <li>For investigations and fraud detection and prevention</li>
                <li>
                  For business transactions provided certain conditions are met
                </li>
                <li>
                  If it is contained in a witness statement and the collection
                  is necessary to assess, process, or settle an insurance claim
                </li>
                <li>
                  For identifying injured, ill, or deceased persons and
                  communicating with next of kin
                </li>
                <li>
                  If we have reasonable grounds to believe an individual has
                  been, is, or may be victim of financial abuse
                </li>
                <li>
                  If it is reasonable to expect collection and use with consent
                  would compromise the availability or the accuracy of the
                  information and the collection is reasonable for purposes
                  related to investigating a breach of an agreement or a
                  contravention of the laws of Canada or a province
                </li>
                <li>
                  If disclosure is required to comply with a subpoena, warrant,
                  court order, or rules of the court relating to the production
                  of records
                </li>
                <li>
                  If it was produced by an individual in the course of their
                  employment, business, or profession and the collection is
                  consistent with the purposes for which the information was
                  produced
                </li>
                <li>
                  If the collection is solely for journalistic, artistic, or
                  literary purposes
                </li>
                <li>
                  If the information is publicly available and is specified by
                  the regulations
                </li>
              </ul>
            </p>
          </ol>
        </div>
      </div>
      <br />
      <Footer />
    </div>
  );
}

export default Privacy;
