import React, { useState} from "react";
import Footer from "./Footer";
import NavBar from "./Navbar";
import { Modal, Button } from "react-bootstrap";
import { PaystackButton } from "react-paystack";
import {useScrollToTop} from '../components/ScrollToTop'

function UserRegister() {
  const setScrollToTop = useScrollToTop(true);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const publicKey = "pk_test_9566bd0a2ae4da67b3679b924b3c1a3ef4a26792";
  const amount = 125;
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");

  const [first_name, setFirstName] = useState("");
  const [last_name, setLastName] = useState("");
  const [registered_email, setRegisteredEmail] = useState("");
  const [phone_number, setPhoneNumber] = useState("");
  const [registerCountry, setUserCountry] = useState("");
  const [user_course, setUserCourse] = useState("");

  const componentProps = {
    email,
    amount,
    metadata: {
      name,
      phone,
    },
    publicKey,
    text: "Pay Now",
    onSuccess: () => alert("Thanks for learning with us! Come back soon!!"),
    onClose: () => alert("Wait! Don't leave :("),
  };

  const submitRequest = async (e) => {
    e.preventDefault();
    const response = await fetch("http://localhost:3001/register/course", {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        registered_email,
        first_name,
        last_name,
        phone_number,
        registerCountry,
        user_course,
      }),
    });
    const resData = await response.json();
    if (resData.status === "success") {
      alert("Your form Sent Sucessfully.");
      
    } else if (resData.status === "fail") {
      alert("Message failed to send.");
    }
    window.location.reload();
  };

  return (
    <div onClick={() => setScrollToTop(true)}>
      <>
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Payment Details</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="container-fluid">
              <form class="row g-2 needs-validation" novalidate>
                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip01"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Name
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="validationTooltip01"
                    placeholder="name"
                    required
                    onChange={(e) => setName(e.target.value)}
                  />
                  <div class="valid-tooltip">Looks good!</div>
                </div>

                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip02"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Phone Number
                  </label>

                  <input
                  type="text"
                  class="form-control"
                  id="validationTooltip07"
                  placeholder="+1 409-444-4444"
                  name="Phone"
                  required
                  onChange={(e) => setPhone(e.target.value)}
                  
                />
                  <div class="invalid-tooltip">
                    please provide correct phone number
                  </div>
                </div>

                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip03"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    class="form-control"
                    id="validationTooltip03"
                    placeholder="email"
                    required
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  <div class="valid-tooltip">Looks good!</div>
                </div>
              </form>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <PaystackButton
              {...componentProps}
              className="btn btn-primary text-light"
            />
          </Modal.Footer>
        </Modal>
      </>

      <section>
        <NavBar />
        <br />
        <div class="container contact-container">
          <section class=" container">
            <p
              class="text-center w-responsive mx-auto mb-5 pt-5"
              style={{ color: "navy" }}
            >
              Please fill all the fields below.
            </p>

            <form
              onSubmit={submitRequest}
              class="row g-3 needs-validation"
              novalidate
            >
              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip01"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  First name
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip01"
                  placeholder="first name"
                  name="first_name"
                  required
                  onChange={(e) => setFirstName(e.target.value)}
                  value={first_name}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>
              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip02"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Last name
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip03"
                  placeholder="last name"
                  name="last_name"
                  required
                  onChange={(e) => setLastName(e.target.value)}
                  value={last_name}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip04"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Email
                </label>
                <input
                  type="email"
                  class="form-control"
                  id="validationTooltip03"
                  placeholder="example@gmail.com"
                  name="registered_email"
                  required
                  onChange={(e) => setRegisteredEmail(e.target.value)}
                  value={registered_email}
                />
                <div class="invalid-tooltip">please provide correct email</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip05"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Phone Number
                </label>

                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip07"
                  placeholder="+1 409-444-4444"
                  name="phone_number"
                  required
                  onChange={(e) => setPhoneNumber(e.target.value)}
                  value={phone_number}
                />
                <div class="invalid-tooltip">
                  please provide correct phone number
                </div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip06"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Country
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip07"
                  placeholder="country name"
                  name="registerCountry"
                  required
                  onChange={(e) => setUserCountry(e.target.value)}
                  value={registerCountry}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip08"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Course
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip02"
                  placeholder="enter course"
                  name="course"
                  required
                  onChange={(e) => setUserCourse(e.target.value)}
                  value={user_course}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-12 mb-5 me-3 d-flex justify-content-between">
                <button
                  class="btn "
                  type="submit"
                  style={{ backgroundColor: "navy", color: "white" }}
                >
                  Submit form
                </button>
                <Button onClick={handleShow}>Pay Now</Button>
              </div>
            </form>
          </section>
        </div>
        <br /> <br /> <br />
        <Footer />
      </section>
    </div>
  );
}

export default UserRegister;
