import React, { useState } from "react";
import IntlTelInput from "react-bootstrap-intl-tel-input";
import { Button, Modal } from "react-bootstrap";
import Footer from "../Footer";
import NavBar from "../Navbar";
import { PaystackButton } from "react-paystack";
import {useScrollToTop} from '../ScrollToTop'

function JavaCart() {
  const [show, setShow] = useState(false);
  const setScrollToTop = useScrollToTop(true);
  
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const publicKey = "pk_test_9566bd0a2ae4da67b3679b924b3c1a3ef4a26792";
  const amount = 125;
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");

  const [JavaFirstName, setJavaFirstName] = useState("");
  const [JavaLastName, setJavaLastName] = useState("");
  const [Java_email, setJavaEmail] = useState("");
  const [JavaPhoneNumber, setJavaPhoneNumber] = useState("");
  const [Java_country, setJavaCountry] = useState("");
  const [Java_course, setJavaCourse] = useState("");

  const componentProps = {
    email,
    amount,
    metadata: {
      name,
      phone,
    },
    publicKey,
    text: "Pay Now",
    onSuccess: () => alert("Thanks for learning with us! Come back soon!!"),
    onClose: () => alert("Wait! Don't leave :("),
  };

  const submitRequest = async (e) => {
    e.preventDefault();
    const response = await fetch(
      "http://localhost:3001/register/java",
      {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({ Java_email, JavaFirstName, JavaLastName, JavaPhoneNumber,Java_country,Java_course }),
      }
    );
    const resData = await response.json();
    if (resData.status === "success") {
      alert("Your form Sent Sucessfully.");
    } else if (resData.status === "fail") {
      alert("Form failed to send.");
    }
    window.location.reload();
  };

  return (
    <div onClick={() => setScrollToTop(true)}>
      <>
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Payment Details</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="container-fluid">
              <form class="row g-2 needs-validation" novalidate>
                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip01"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Name
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="validationTooltip01"
                    placeholder="name"
                    required
                    onChange={(e) => setName(e.target.value)}
                  />
                  <div class="valid-tooltip">Looks good!</div>
                </div>

                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip02"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Phone Number
                  </label>

                  <IntlTelInput
                    preferredCountries={["US", "GB"]}
                    defaultCountry={"US"}
                    defaultValue={"+1 409-444-4444"}
                    onPhoneNumberChange={(e) => setPhone(e.target.value)}
                  />
                  <div class="invalid-tooltip">
                    please provide correct phone number
                  </div>
                </div>

                <div class="col-md-4 position-relative">
                  <label
                    for="validationTooltip03"
                    class="form-label"
                    style={{ color: "navy" }}
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    class="form-control"
                    id="validationTooltip03"
                    placeholder="email"
                    required
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  <div class="valid-tooltip">Looks good!</div>
                </div>
              </form>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <PaystackButton
              {...componentProps}
              className="btn btn-primary text-light"
            />
          </Modal.Footer>
        </Modal>
      </>

      <section>
        <NavBar />
        <br />
        <div class="container contact-container">
          <section class=" container">
            <p
              class="text-center w-responsive mx-auto mb-5 pt-5"
              style={{ color: "navy" }}
            >
              Please fill all the fields below.
            </p>

            <form
              onSubmit={submitRequest}
              class="row g-3 needs-validation"
              novalidate
            >
              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip01"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  First name
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip01"
                  placeholder="first name"
                  name="JavaFirstName"
                  required
                  onChange={(e) => setJavaFirstName(e.target.value)}
                  value={JavaFirstName}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>
              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip02"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Last name
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip03"
                  placeholder="last name"
                  name="JavaLastName"
                  required
                  onChange={(e) => setJavaLastName(e.target.value)}
                  value={JavaLastName}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip04"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Email
                </label>
                <input
                  type="email"
                  class="form-control"
                  id="validationTooltip03"
                  placeholder="example@gmail.com"
                  name="Java_email"
                  required
                  onChange={(e) => setJavaEmail(e.target.value)}
                  value={Java_email}
                />
                <div class="invalid-tooltip">please provide correct email</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip05"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Phone Number
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip07"
                  placeholder="+1 409-444-4444"
                  name="JavaPhoneNumber"
                  required
                  onChange={(e) => setJavaPhoneNumber(e.target.value)}
                  value={JavaPhoneNumber}
                />
               
                <div class="invalid-tooltip">
                  please provide correct phone number
                </div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip06"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Country
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip07"
                  placeholder="country name"
                  name="Java_country"
                  required
                  onChange={(e) => setJavaCountry(e.target.value)}
                  value={Java_country}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-md-4 position-relative">
                <label
                  for="validationTooltip08"
                  class="form-label"
                  style={{ color: "navy" }}
                >
                  Course
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip02"
                  placeholder="enter course here..."
                  name="Java_course"
                  required
                  onChange={(e) => setJavaCourse(e.target.value)}
                  value={Java_course}
                />
                <div class="valid-tooltip">Looks good!</div>
              </div>

              <div class="col-12 mb-5 me-3 d-flex justify-content-between">
                <button
                  class="btn "
                  type="submit"
                  style={{ backgroundColor: "navy", color: "white" }}
                >
                  Submit form
                </button>
                <Button onClick={handleShow}>Pay Now</Button>
              </div>
            </form>
          </section>
        </div>
        <br /> <br /> <br />
        <Footer />
      </section>
    </div>
  );
}

export default JavaCart;
